/* ******** */
/* common */
/* ******** */
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
		"Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

li:hover {
	opacity: 0.9;
}

li:active {
	opacity: 1;
}

li {
	list-style: none;
}

.error {
	color: red;
	text-align: center;
}

/* ******** */
/* search input */
/* ******** */
input {
	font-size: 20px;
	padding: 10px 20px;
	min-width: 500px;
	margin: 30px auto;
	display: block;
	text-align: center;
}
input.error {
	border: 2px solid red;
}

/* ******** */
/* movies */
/* ******** */
.movies {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
	grid-gap: 15px;
	padding: 0 100px 50px;
}
.movies li {
	/* width: 20%; */
	position: relative;
}
.movies li img {
	width: 100%;
}
.movies li h2 {
	font-size: 15px;
	padding: 10px;
	background: rgba(255, 255, 255, 0.9);
	border: 1px solid #ccc;
	border-bottom: none;
	position: absolute;
	top: 0;
	width: 100%;
	display: none;
}
.movies li:hover h2 {
	display: block;
}

.loading {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
